<template>
<section>
	<section class="foot_head">
		<router-link to="/talkline" class="telcon"></router-link>
		<router-link to="/ouraddress" class="ouraddress"></router-link>	
		
	</section>
	<section class="foot_change">
		<span></span>
		<p>
		<i>移动版</i>
		<i>标准版</i>
		</p>
		<p><em>瑞德启蒙教育&copy; 2013 版权所有</em></p>
	</section>
	
</section>
</template>

<script>
export default{
	name:"footer",
	data(){
		return{
			msg:"footer",
			listData:[]
		}
	},
	mounted(){//装在完毕是出发
		let _this=this;
		_this.$http({
			method:"get",
			url:'./static/api/list.json',
		}).then(function(res){//reseponse响应对象即res
			_this.listData=res.data

		},function(err){
			console.log(err);
		})
	}
}
	
</script>

<style lang="scss" scoped>
@import "../assets/css/px2rem.scss";
@import "../assets/css/style.scss";	
</style>


