<template>
  <section class="ouraddress">
      <section class="adress_title"><h3>中心位置查询</h3></section>
      <span class="adress_titleline"></span>
     
      <section class="search_addtel">
       <DropdownBox class="DropdownBox"></DropdownBox>
       <p><span>ADD:</span>深圳市宝安区这行字显示你选择的中心的地址有
          时可能要两行</p>
        <p><span>TEL:</span>86-755-9999999</p>
       </section>
       <section class="map">
       <img src="../assets/img/map.png" alt="">
       </section>
  </section>
</template>

<script>
import DropdownBox from "@/components/common/DropdownBox";
export default {
  name: 'ouraddress',
  data() {
    return {
      msg: 'ouraddress',
      listData:[]
    }
  },
  mounted(){//装载完毕的时候触发
    let _this=this;
    _this.$http({
      method:"get",
      url:'./static/api/list.json', 
    }).then(function(res){//res响应对象
      _this.listData=res.data
    },function(err){
      console.log(err);
    });
  },
  components:{
    DropdownBox,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../assets/css/font-awesome.min.css";
@import "../assets/css/px2rem.scss";
@import "../assets/css/animate.css";
.adress_title{line-height:rem(66px);width:100%;height:rem(66px);background:url(../assets/img/footbg.jpg);background-size:rem(8px) rem(67px);
  h3{color:#3c3535;font-weight:normal;width:rem(176px);@include font-dpr(30px);margin-left:rem(30px);text-align:left;}
}
.adress_titleline{display:block;width:100%;height:rem(2px);background:#00afd5;border-top:1px #0b6aa6 solid;border-bottom:1px #0b6aa6 solid;}
.search_addtel{width:100%;height:rem(203px);}
.DropdownBox{margin-top:rem(30px);margin-bottom:rem(18px);}
p{@include font-dpr(24px);line-height:rem(36px);width:rem(507px);text-align:left;margin-left:rem(104px);position:relative; span{color:#009bda;position:absolute;left:rem(-60px);}color:#787878;}
.map{background:#fff;height:rem(540px);display:flex;align-items:center;img{height:rem(504px);width:rem(640px);}}
$red:#f00;
a{
  color:$red;
}
</style>



