<template>
<section class="header">
	<a @touchstart.prevent="appMove" class="fa fa-th-list" href="javascript:void(0)"></a>
	<p></p>
	<a class="telbt fa fa-phone-square" href="tel:13456929508"></a>
	<nav class="nav" ref="nav" :style="{marginLeft:navMove+'px'}">
	<router-link v-for="(item,key) in navData" :to="item.url" :key="key"><i class="fa" :class="item.className"> {{item.name}}</i></router-link>
			
	</nav>
</section>
</template>

<script>
import Bus from "@/assets/js/bus.js";
export default{
	name:"header",
	props:{
		navMove:{
			type:Number,
			default:0
		}
	},
	data(){
		return{
			navData:[
			{
				url:"/",
				name:"首页",
				className:"fa-home"
			},
			{
				url:"/brand",
				name:"品牌",
				className:"fa-cube"
			},
			{
				url:"/course",
				name:"课程介绍",
				className:"fa-book"
			},
			{
				url:"/activity",
				name:"活动资讯",
				className:"fa-location-arrow"
			},
			{
				url:"/mba",
				name:"父母MBA",
				className:"fa-university"
			},
			{
				url:"/nuews",
				name:"新闻中心",
				className:"fa-newspaper-o"
			},
			{
				url:"/order",
				name:"预约试听",
				className:"fa-first-order"
			}

			]
		}
	},
	methods:{
		appMove(e){

			let navNode=this.$refs.nav;
			let width=navNode.clientWidth;
			Bus.$emit("move",width);
		}
	},
	mounted(){//装在完毕是出发
		let _this=this;
		_this.$http({
			method:"get",
			url:'./static/api/list.json',
		}).then(function(res){//reseponse响应对象即res
			_this.listData=res.data

		},function(err){
			console.log(err);
		});



	}
}
	
</script>

<style lang="scss" scoped>
@import "../assets/css/px2rem.scss";
@import "../assets/css/style.scss";	
.header{z-index:99;}
.nav{z-index:1000;transition:margin-left 0.2s;}
</style>


