<template>
  <section class="activitylist">
     
       <refreshMore :ajaxParam="ajaxParam">
       <latestactivities></latestactivities>
       <vipactivities></vipactivities>
       <huiyuan></huiyuan>
       </refreshMore>
  </section>
</template>

<script>
import latestactivities from "@/components/common/latestactivities";
import vipactivities from "@/components/common/vipactivities";
import huiyuan from "@/components/common/huiyuan";
import refreshMore from "@/components/common/refreshMore"


export default {
  name: 'activitylist',
  data() {
    return {
      msg: 'activitylist',
      activitylistData:[],
      ajaxParam:{
        url:"./static/api/huiyuan.json",
        pageNum:1
      }
    }
  },
  mounted(){//装载完毕的时候触发
    
  },
  components:{
    latestactivities,
    vipactivities,
    huiyuan,
    refreshMore
  },
  computed:{
    dataJson(){
        return this.$store.state.refreshMore.dataJson;
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../assets/css/font-awesome.min.css";
@import "../assets/css/px2rem.scss";
@import "../assets/css/animate.css";
</style>


