<template>
  <ul class="index_list">
      <li v-for="(item,key) in indexlistData" :key="key"><router-link :to="item.url"><p class="fa" :class="item.className"></p><span>{{item.name}}</span></router-link></li>
  </ul>
</template>

<script>

export default{
	name:"indexlist",
	data(){
		return{
			indexlistData:[
			
			{
				url:"/brand",
				name:"瑞德品牌",
				className:"fa-cube"
			},
			{
				url:"/course",
				name:"课程介绍",
				className:"fa-book"
			},
			{
				url:"/activity",
				name:"活动资讯",
				className:"fa-location-arrow"
			},
			{
				url:"/mba",
				name:"父母MBA",
				className:"fa-university"
			},
			{
				url:"/nuews",
				name:"新闻中心",
				className:"fa-newspaper-o"
			},
			{
				url:"/order",
				name:"预约试听",
				className:"fa-first-order"
			}

			]
		}
	},
	methods:{
		appMove(){
			let navNode=this.$refs.nav;
			let width=navNode.clientWidth;
			Bus.$emit("move",width);
		}
	},
	mounted(){//装在完毕是出发
		let _this=this;
		_this.$http({
			method:"get",
			url:'./static/api/list.json',
		}).then(function(res){//reseponse响应对象即res
			_this.listData=res.data

		},function(err){
			console.log(err);
		})
	}
}
	
</script>
<style lang="scss" scoped>
	@import "../assets/css/px2rem.scss";
	@import "../assets/css/style.scss";	
	.index_list{
		display:flex;
		justify-content:space-around;
		flex-wrap:wrap;
		width:rem(619px);
		margin-left:rem(11px);
		margin-top: rem(14px);
		li{
			width:rem(192px);height:rem(148px);margin-bottom:rem(24px);border-radius:rem(4px);
			a{display:flex;width:100%;height:100%;color:#ffffff;justify-content:center;flex-wrap:wrap;
				.fa{@include font-dpr(70px);margin-top:rem(34px);}
				span{@include font-dpr(18px);width:100%;}
			}
		}
		li:nth-child(1){background:#0bbebb};
		li:nth-child(2){background:#afd23a};
		li:nth-child(3){background:#f48221};
		li:nth-child(4){background:#de0000};
		li:nth-child(5){background:#3eb7ee};
		li:nth-child(6){background:#6eb943};
	}

</style>


