<template>
  <section class="newactivity">
      <h3>最新活动</h3>
      <i></i>
      <ul>
      <li v-for="(item,key) in newactivityData" :key="key">
      <h1>{{item.name}}</h1>
      <img :src="item.imgurl" alt="" class="newactivitypic">
      <p>{{item.words}}</p>  
      <router-link :to="item.linkUrl">报名参加</router-link>
      </li>
      </ul>

  </section>
</template>

<script>
export default {
  name: 'newactivity',
  data() {
    return {
      msg: 'newactivity',
      newactivityData:[]
    }
  },
  mounted(){//装载完毕的时候触发
    let _this=this;
    _this.$http({
      method:"get",
      url:'./static/api/newactivity.json'
    }).then(function(res){//res响应对象
      _this.newactivityData=res.data;
      console.log(res.data[0]);
    },function(err){
      console.log(err);
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../assets/css/font-awesome.min.css";
@import "../assets/css/px2rem.scss";
@import "../assets/css/animate.css";

.brandhead{width:rem(640px);height:rem(183px);}
h3{height:rem(67px);background:url(../assets/img/footbg.jpg);background-size:rem(8px) rem(67px);color:#3c3535;@include font-dpr(30px);line-height:rem(67px);text-align:left;padding-left:rem(30px);}
i{display:block;width:100%;height:rem(2px);border-top:1px #0b6aa6 solid;border-bottom:1px #0b6aa6 solid;background:#00afd5;margin-bottom:rem(33px);}
li{h1{@include font-dpr(36px);line-height:rem(42px);color:#009bda;text-align:left;padding-left:rem(29px);margin-bottom:rem(50px);}
img{width:rem(584px);height:rem(235px);margin-top:rem(17px);margin-bottom:rem(13px);}
p{@include font-dpr(24px);line-height:rem(42px);width:rem(584px);text-align:left;padding-left:rem(29px);text-indent:rem(45px);}
}
 a{display:block;width:rem(563px);height:rem(70px);background:url(../assets/img/btnbg.png);background-size:rem(11px) rem(70px);border:1px #0895d1 solid;border-radius:rem(4px);line-height:rem(70px);color:#fff;@include font-dpr(29px);margin:rem(46px) auto rem(62px);}

</style>



