  label{display:block;width:auto;height:auto;@include font-dpr(20px);text-align:left;box-sizing:border-box;padding-left:rem(48px);margin-bottom:rem(14px);line-height:rem(42px);}
  input{width:rem(373px);height:rem(42px);box-sizing:border-box;border:1px #d8d8d8 solid;padding-left:rem(15px);@include font-dpr(20px);margin-left:rem(24px);
   
}
  .styled-select select { box-sizing:border-box;background: transparent; width: rem(147px); height:rem(43px); padding:rem(5px); @include font-dpr(20px); border: 1px solid #ccc;color:#c5c5c5;
