.styled-select_shot{width:rem(100px);select{width:rem(100px);}}
.select_label{display:flex;}
.styled_address{width:rem(373px);height:rem(42px);box-sizing:border-box;select{width:rem(373px);height:rem(42px);}}
.select_tit{color:#c5c5c5}
.ask_check{input{width:rem(22px);height:rem(22px);}display:flex;align-items:center;}
a{display:block;width:rem(490px);height:rem(64px);background:url(../../assets/img/loginbtn.jpg);background-size:rem(6px) rem(64px);color:#fff;line-height:rem(64px);margin-left:rem(50px);position: absolute;bottom:rem(40px);}
}

i{display:block;width:rem(594px);height:rem(8px);background:#443c3c;border-radius:0 0 rem(4px) rem(4px);}
