<template>
  <section class="registration">
  <input type="text" placeholder="用户名：">
  <input type="password" placeholder="密 码：">
  <input type="submit" value="登陆" class="loginbt">
  <router-link to="/">注册</router-link>   
  </section>
</template>

<script>
export default {
  name: 'registration',
  data() {
    return {
      msg: 'registration',
      registrationData:[]
    }
  },
  mounted(){//装载完毕的时候触发
    let _this=this;
    _this.$http({
      method:"get",
      url:'./static/api/registration.json'
    }).then(function(res){//res响应对象
      _this.registrationData=res.data;
      console.log(res.data);
    },function(err){
      console.log(err);
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../assets/css/font-awesome.min.css";
@import "../assets/css/px2rem.scss";
@import "../assets/css/animate.css";
.registration{width:100%;height:rem(845px);background:url(../assets/img/loginbg.jpg);background-size:rem(640px) rem(845px);box-sizing:border-box;padding-top:rem(194px);}
input{width:rem(574px);height:rem(86px);border-radius:rem(3px);border:1px #d4d4d4 solid;color:#646565;@include font-dpr(30px);box-sizing:border-box;padding-left:rem(34px);line-height:rem(86px);margin-bottom:rem(16px);}
.loginbt{width:rem(490px);height:rem(64px);background:url(../assets/img/loginbtn.jpg);background-size:rem(6px) rem(64px);border-radius:rem(3px);margin-top:rem(16px);margin-bottom:rem(18px);color:#fff;line-height:rem(64px);padding:0;}
a{display:block;width:rem(490px);height:rem(64px);background:url(../assets/img/zhuce.jpg);background-size:rem(6px) rem(64px);border-radius:rem(3px);color:#fff;@include font-dpr(30px);margin-left:rem(73px);line-height:rem(64px);}
</style>
 


