<template>
  <section class="selectedbox">
    <span></span> 
    <select v-model="data" class="selected">s
     <option v-for="item in data" v-bind:value="item.value">{{item.text}}</option>  
    </select>
       
    
  </section>
</template>

<script>

export default {
  name: 'DropdownBox',
  data() {
    return {
      data:[
      {
        text:'北京',value:'bj'
      },
      {
        text:'上海',value:'sh'
      },
      {
        text:'杭州',value:'hz'
      }
      ]
     
    }
  },
 
  methods:{
    
  }
  
}
</script>


<style lang="scss" scoped>
@import "../../assets/css/font-awesome.min.css";
@import "../../assets/css/px2rem.scss";
@import "../../assets/css/animate.css";
.selectedbox{position: relative;}
.selected{
 width:rem(540px);height:rem(60px);border:1px #d8d8d8 solid;border-right:0px;
 option{width:rem(541px);}
}
span{display:block;width:rem(65px);height:rem(60px);background:url(../../assets/img/selectedbg.png);background-size:rem(65px) rem(60px);top:0;right:rem(50px);position:absolute;z-index:99;}
 
	
</style>


