<template>
  <section class="mbalist">
      <h3 v-if="title">{{title}}</h3>
      <i v-if="title"></i>
      <ul>
        <li v-for="(item,key) in mbalistData" :key="key"><router-link :to="item.linkUrl" :class="item.class"><span>{{item.name}}</span></router-link></li>
      </ul>
  </section>
</template>

<script>
export default {
  name: 'mbalist',
  props:{
    url:{
      type:String,
      default:""
    },
    title:String
  },
  data() {
    return {
      msg: 'mbalist',
      mbalistData:[]
    }
  },
  mounted(){//装载完毕的时候触发
    let _this=this;
    _this.$http({
      method:"get",
      url:this.url
    }).then(function(res){//res响应对象
      _this.mbalistData=res.data;
      console.log(res.data);
    },function(err){
      console.log(err);
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/font-awesome.min.css";
@import "../../assets/css/px2rem.scss";
@import "../../assets/css/animate.css";

.brandhead{width:rem(640px);height:rem(183px);}
.characteristic{margin-bottom:rem(70px);}
h3{height:rem(67px);background:url(../../assets/img/mbabtn.jpg);background-size:rem(640px) rem(67px);color:#454545;@include font-dpr(30px);line-height:rem(67px);text-align:left;box-sizing:border-box;padding-left:rem(60px);}
i{display:block;width:100%;height:rem(2px);border-top:1px #0b6aa6 solid;border-bottom:1px #0b6aa6 solid;background:#00afd5;}
ul{display:flex;justify-content:center;flex-wrap:wrap;}
li{width:rem(640px);height:rem(66px);border-bottom:1px #d4d4d4 solid;
  a{display:block;width:100%;height:100%;display:flex;align-items:center;color:#3c3535;background:#f1f2f2;
    span{@include font-dpr(30px);margin-left:rem(53px);}
  }
  .hasbg{background:url(../../assets/img/mablist.jpg) no-repeat;background-size:rem(640px) rem(66px);}
 
}
</style>



