<template>
<section>
<h1>这是个list页面</h1>
{{msg}}
<section><i class="fa fa-calendar">kankan</i></section>
	
<ul class="ullist">
	<li v-for="item in listData"></li>
</ul>
</section>
</template>

<script>
export default{
	name:"list",
	data(){
		return{
			msg:"list",
			listData:[]
		}
	},
	mounted(){//装在完毕是出发
		let _this=this;
		_this.$http({
			method:"get",
			url:'./static/api/list.json',
		}).then(function(res){//reseponse响应对象即res
			_this.listData=res.data

		},function(err){
			console.log(err);
		})
	}
}
	
</script>

<style lang="scss" scoped>
	$red:#ff0;
	i{
		color:$red;
	}
	.ullist{background:url(../assets/img/11.png)}
</style>


