<template>
  <section class="huiyuan">
      <h3>会员活动区</h3>
      <i></i>
      <ul>
        <li v-for="(item,key) in huiyuanData" :key="key">
        <img :src="item.imgurl" alt="">
        <span></span>
        <p>{{item.time}}</p>
        <p>{{item.person}}</p>
        <p>{{item.address}}</p>
        <router-link :to="item.linkUrl">报名参加</router-link>
        </li>
      </ul>
  </section>
</template>

<script>
export default {
  name: 'huiyuan',
  data() {
    return {
      msg: 'huiyuan',
      huiyuanData:[]
    }
  },
  mounted(){//装载完毕的时候触发
    let _this=this;
    _this.$http({
      method:"get",
      url:'./static/api/huiyuan.json'
    }).then(function(res){//res响应对象
      _this.huiyuanData=res.data;
      console.log(res.data);
    },function(err){
      console.log(err);
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/font-awesome.min.css";
@import "../../assets/css/px2rem.scss";
@import "../../assets/css/animate.css";
.huiyuan{padding-bottom:rem(55px);}
.brandhead{width:rem(640px);height:rem(183px);}
h3{height:rem(67px);background:url(../../assets/img/footbg.jpg);background-size:rem(8px) rem(67px);color:#008dce;@include font-dpr(30px);line-height:rem(67px);text-align:left;padding-left:rem(30px);}
i{display:block;width:100%;height:rem(2px);border-top:1px #0b6aa6 solid;border-bottom:1px #0b6aa6 solid;background:#00afd5;margin-bottom:rem(20px);}
ul{display:flex;justify-content:center;flex-wrap:wrap;}
li{width:rem(592px);height:rem(512px);border-radius:rem(6px);margin-bottom:rem(38px);padding-top:rem(9px);background:#fff;position:relative;
  span{width:rem(80px);height:rem(75px);background:url(../../assets/img/vip.png) no-repeat;background-size:rem(80px) rem(75px);position:absolute;top:0;left:0;}
  img{width:rem(576px);height:rem(278px);margin-bottom:rem(9px);}
  p{@include font-dpr(24px);line-height:rem(36px);text-align:left;padding-left:rem(29px);}
  a{display:block;width:rem(563px);height:rem(70px);background:url(../../assets/img/btnbg.png);background-size:rem(11px) rem(70px);border:1px #0895d1 solid;border-radius:rem(4px);line-height:rem(70px);color:#fff;@include font-dpr(29px);margin:rem(22px) auto 0;}
}
</style>



