<template>
 <section>
    <div class="loginname">登录名：<input type="text" ref="name" v-model="name"></div>
    <textarea name="" id="" cols="30" rows="10" ref="textarea" v-model="val"></textarea>
    <button @touchstart="face" class="face">表情</button>
    <div v-show="faceShowHide" class="facediv">
    <img @touchstart="faceInsert(items)" v-for="(items,key) in allFace" :key="key" :src="'./static/face/'+items+'.png'" />
    </div>
      <button @touchstart="submit" class="submit">提交</button>
      <ul>
        <li v-for="(item,key) in talkdata" :key="key" v-html="item.name+':'+faceHtml(item.val)"></li>
      </ul>
 </section>
</template>

<script>
import io from "socket.io-client";
export default {
  name: 'talkline',
  data() {
    return {
      msg: 'talkline',
      name:"",
      val:"",
      ws:null,
      talkdata:[{name:"11",val:"haha"}],
      faceShowHide:false,
      allFace:[
        "$wx$",
        "$yx$"
      ]
    }
  },
  components:{
   
    },
   mounted(){//装载完毕的时候触发
    let _this=this;
    _this.ws=io("http://10.66.4.11:8083");
    _this.ws.on("connect",function(){
      console.log(_this.ws.id,"111",name);//id值
      _this.ws.on("allChat",function(data){
        console.log(data);
        _this.talkdata.push(data)
      })
    });
  }, 
  methods:{
    submit(){
      let val=this.val;
      let name=this.name;
      console.log(val,name)
      console.log(this.ws)
      this.ws.emit("chat",{
        name,
        val
      })

    },
      face(){
      this.faceShowHide=!this.faceShowHide;
    },
    faceInsert(faceName){
      this.val+=faceName;
      console.log(this.val)
    },
    faceHtml(text){
      var reg=/\$[a-z]{2}\$/g;
      var _this=this;
      var tagReg=/\<|\>/g;
      text=text.replace(tagReg,function(word){
        var str;
        if(word=="<"){
          str="&lt;";
        }
        else{
          str="&gt;";
        }
        return str;
      });
      var html=text.replace(reg,function(word){
        var str;
        if(_this.allFace.indexOf(word)==-1){
          str=word;
        }
        else{
          str=`<img src="./static/face/${word}.png" />`
        }
        
        return str;
      });
      return html;
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../assets/css/px2rem.scss";
textarea{position:absolute;bottom:rem(300px);width:rem(600px);height:rem(200px);left:50%;margin-left:rem(-300px);border:1px #000 solid;}
button{position:absolute;bottom:rem(250px);width:rem(100px);left:50%;}
.face{margin-left:rem(-120px);}
.facediv{position:absolute;bottom:rem(300px);width:rem(600px);left:50%;margin-left:rem(-300px);border:1px #000 solid;}
.loginname{position:absolute;bottom:rem(520px);margin-left:rem(20px);}
ul{position:absolute;bottom:rem(600px);width:rem(600px);left:50%;margin-left:rem(-300px);text-align:left;}
input{border:1px #000 solid;}
</style>



