<template>
  <section class="course">
      <img class="brandhead" src="../assets/img/abouthead.png" alt="">
      <characteristic :url="urlcharacteristic" title="特色课程"></characteristic>
      <characteristic :url="urlbasiccourse" title="启蒙基础课程"></characteristic>
      
  </section>
</template>

<script>
import characteristic from "@/components/common/characteristic";

export default {
  name: 'course',
  data() {
    return {
      msg: 'course',
      courseData:[],
      urlcharacteristic:"./static/api/characteristic.json",
      urlbasiccourse:"./static/api/basiccourse.json"

    }
  },
  mounted(){//装载完毕的时候触发
    
  },
  components:{
    characteristic
    

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../assets/css/font-awesome.min.css";
@import "../assets/css/px2rem.scss";
@import "../assets/css/animate.css";
.course{height:rem(1450px);}
.brandhead{width:rem(640px);height:rem(183px);}

</style>



