<template>
  <section class="characteristic">
      <h3 v-if="title">{{title}}</h3>
      <i v-if="title"></i>
      <ul>
        <li v-for="(item,key) in characteristicData" :key="key"><router-link :to="item.linkUrl"><span>{{item.name}}</span></router-link></li>
      </ul>
  </section>
</template>

<script>
export default {
  name: 'characteristic',
  props:{
    url:{
      type:String,
      default:""
    },
    title:String
  },
  data() {
    return {
      msg: 'characteristic',
      characteristicData:[]
    }
  },
  mounted(){//装载完毕的时候触发
    let _this=this;
    _this.$http({
      method:"get",
      url:this.url
    }).then(function(res){//res响应对象
      _this.characteristicData=res.data;
      console.log(res.data);
    },function(err){
      console.log(err);
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/font-awesome.min.css";
@import "../../assets/css/px2rem.scss";
@import "../../assets/css/animate.css";

.brandhead{width:rem(640px);height:rem(183px);}
.characteristic{margin-bottom:rem(70px);}
h3{height:rem(67px);background:url(../../assets/img/footbg.jpg);background-size:rem(8px) rem(67px);color:#008dce;@include font-dpr(30px);line-height:rem(67px);}
i{display:block;width:100%;height:rem(2px);border-top:1px #0b6aa6 solid;border-bottom:1px #0b6aa6 solid;background:#00afd5;}
ul{margin-top:rem(47px);display:flex;justify-content:center;flex-wrap:wrap;}
li{width:rem(574px);height:rem(86px);border:1px #d4d4d4 solid;border-radius:rem(4px);margin-bottom:rem(3px);
  a{display:block;width:100%;height:100%;background:url(../../assets/img/listbg1.png) no-repeat;background-size:rem(574px) rem(86px);display:flex;align-items:center;justify-content:center;color:#3c3535;
    span{@include font-dpr(30px);}
  }
  a:hover{background:url(../../assets/img/listbg2.png) no-repeat;background-size:rem(574px) rem(86px);color:#fff;}
}
</style>



