 
<template>
  <section class="list">
      <img class="brandhead" src="../assets/img/abouthead.png" alt="">
      <characteristic :url="urlbrand" title=""></characteristic>

  </section>
</template>

<script>
import characteristic from "@/components/common/characteristic";
export default {
  name: 'brand',
  data() {
    return {
      msg: 'brand',
      listData:[],
      urlbrand:"./static/api/list.json"
    }
  },
  components:{
    characteristic
    },
  mounted(){//装载完毕的时候触发
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../assets/css/font-awesome.min.css";
@import "../assets/css/px2rem.scss";
@import "../assets/css/animate.css";

.brandhead{width:rem(640px);height:rem(183px);}

</style>



