import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import brand from '@/components/brand'
import list from '@/components/list'
import ouraddress from '@/components/ouraddress'
import activity from '@/components/activity'
import course from '@/components/course'
import activitylist from '@/components/activitylist'
import newactivity from '@/components/newactivity'
import detail from '@/components/detail'
import registration from '@/components/registration'
import signup from '@/components/signup'
import mba from '@/components/mba'
import nuews from '@/components/nuews'
import order from '@/components/order'
import talkline from '@/components/talkline'









Vue.use(Router);//让router装载到框架

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
     {
      path: '/brand',
      name: 'brand',
      component: brand
    },
    {
      path: '/ouraddress',
      name: 'ouraddress',
      component: ouraddress
    },
    {
      path: '/activity',
      name: 'activity',
      component: activity
    },
    {
      path: '/course',
      name: 'course',
      component: course
    },
    {
      path: '/activity/activitylist',
      name: 'activitylist',
      component: activitylist
    },
     {
      path: '/activity/newactivity',
      name: 'newactivity',
      component: newactivity
    },
    {
      path: '/detail',
      name: 'detail',
      component: detail
    },
    {
      path: '/registration',
      name: 'registration',
      component: registration
    },
    {
      path: '/signup',
      name: 'signup',
      component: signup
    },
    {
      path: '/mba',
      name: 'mba',
      component: mba
    },
    {
      path: '/nuews',
      name: 'nuews',
      component: nuews
    },
    {
      path: '/order',
      name: 'order',
      component: order
    },
    {
      path: '/talkline',
      name: 'talkline',
      component: talkline
    }
  ]
})



